export default {
  namespaced: true,
  state: {
    shifts: [
      {
        number: 1,
        date: '2&nbsp;июня — 14&nbsp;июня',
        startDay: 12,
        lastDay: 12,
        price: 29800,
      },
      {
        number: 2,
        date: '17&nbsp;июня — 29&nbsp;июня',
        startDay: 12,
        lastDay: 12,
        price: 29300
      },
      {
        number: 3,
        date: '2&nbsp;июля — 14&nbsp;июля',
        startDay: 12,
        lastDay: 12,
        price: 29000
      },
    ]
  },
  mutations: {
    SET_SHIFTS(state, payload) {
      state.shifts = payload;
    }
  },
  actions: {
    fetchShifts ({ commit }) {
      fetch(process.env.VUE_APP_DOMAIN, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json'
        }
      }).then((response) => {
        return response.json();
      }).then((data) => {
        console.log(data);
        commit('SET_SHIFTS', data)
      });
    },
  },
  getters: {
    GET_SHIFTS: (state) => {
      return state.shifts;
    }
  }
};