export default {
  namespaced: true,
  state: {
    trips: [
      {
        id: 0,
        number: 2586221252,
        date: '01 июня – 21 июня 2021',
        price: 28704,
        status: 'Ожидает доплаты'
      },
      {
        id: 1,
        number: 2947223452,
        date: '01 июня – 24 июня 2021',
        price: 20301,
        status: 'Ожидает доплаты'
      },
      {
        id: 2,
        number: 2586234579,
        date: '01 июня – 27 июня 2021',
        price: 50700,
        status: 'Ожидает доплаты'
      },
    ],
    foundTrips: null
  },
  mutations: {
    SET_TRIPS(state, payload) {
      state.trips = payload;
    },
    SET_FOUNDTRIPS(state, payload) {
      state.foundTrips = payload;
    }
  },
  actions: {
    searchTrips ({ commit, getters }, id) {
      commit('SET_FOUNDTRIPS', getters.GET_TRIPS.find( item => item.id == id))
    },

  },
  getters: {
    GET_TRIPS: (state) => {
      return state.trips;
    },
    GET_FOUNDTRIPS: (state) => {
      return state.foundTrips;
    }
  }
};